.top-menu {
  padding: 25px 0;
  border-bottom: 0.5px solid $dc-background-dark-blue;

  @include media-breakpoint-down(md) {
    padding: 15px 0;
  }

  .top-menu-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    max-width: 1600px;
    margin: auto;
  }

  .logo-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      margin-right: 30px;

      .desktop {
        svg {
          width: 250px;
        }
      }

      .mobile {
        display: none;

        svg {
          width: 35px;
        }
      }

      @include media-breakpoint-down(md) {
        .desktop {
          display: none;
        }

        .mobile {
          display: block;
        }
      }
    }

    nav {
      @include mobile-1280 {
        display: none;
      }

      ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
          display: inline-block;

          &.current-menu-item a {
            color: $dc-primary;
            font-weight: $dc-semi-bold;
          }

          &.mega-menu-link {
            position: relative;
            margin-right: 30px !important;

            &.active {
              &:after {
                background-image: url('data:image/svg+xml;base64,') !important;
                width: 15px;
                height: 15px;
              }

              a {
                color: $dc-primary !important;
              }
            }

            &:after {
              content: "";
              position: absolute;
              right: -20px;
              width: 13px;
              height: 7px;
              top: 50%;
              transform: translateY(-50%);
              background-image: url('data:image/svg+xml;base64,');
              background-repeat: no-repeat;
              background-position: center;
              background-size: 100% 100%;
            }
          }

          &:not(:last-child) {
            margin-right: 15px;
          }

          a {
            text-decoration: none;
            transition: $dc-transition-all;

            @include dc-p($dc-text-dark);

            &:hover {
              color: $dc-primary  !important;
            }
          }
        }
      }
    }
  }

  .ctas {
    display: flex;
    gap: 20px;

    @include mobile-1280 {
      display: none;
    }

    .button {
      @include dc-button($dc-primary, darken($dc-primary, 10%));
      display: flex;
      align-items: center;
    }

    .button-secondary {
      i {
        margin-right: 5px;

        path {
          transition: $dc-transition-all;
        }
      }

      &:hover path {
        fill: lighten($dc-secondary, 25%);
      }

      @include dc-button-secondary($dc-secondary, lighten($dc-secondary, 25%));
    }
  }

  .mobile-menu {
    display: none;

    @include mobile-1280 {
      display: block;
    }

    > div {
      cursor: pointer;
    }

    .close {
      display: none;
    }

    svg {
      width: 30px;
      height: 30px;

      path {
        fill: $dc-text-dark;
      }
    }
  }
}